import { useEffect, useState, useRef } from "react"
import { Card, Statistic, Alert } from 'antd';
import { HomeOutlined, GlobalOutlined, WifiOutlined, EnvironmentOutlined } from '@ant-design/icons';
import HCaptcha from '@hcaptcha/react-hcaptcha';

import "./SendResult.scss"
import Category from "../../../components/Category";
import MessageHub from "../../../components/MessageHub";

function SendResult({ result }) {

    const [ipAddress, setIPAddress] = useState('')
    const [ip6Address, setIP6Address] = useState('')
    const [country, setCountry] = useState('')
    const [region, setRegion] = useState('')

    useEffect(() => {
        fetch('https://api.ipify.org?format=json')
            .then(response => response.json())
            .then(data => {
                setIPAddress(data.ip)
            })
            .catch(error => console.log(error))

        fetch('https://api64.ipify.org?format=json')
            .then(response => response.json())
            .then(data => {
                setIP6Address(data.ip)
            })
            .catch(error => console.log(error))

        fetch(`https://api.geoapify.com/v1/ipinfo?&apiKey=6e4ad53275834f7aab02fc3e3d48b0a0`)
            .then(response => response.json())
            .then(data => {
                setCountry(data.country.name_native)
            })
            .catch(error => console.log(error))
    }, [])

    const cardTitleStyles = {
        fontFamily: "Lexend",
        color: "rgb(0 0 0 /70%)",
        fontWeight: 400,
        display: "flex",
        flexDirection: "column",
        gap: "1rem"
    }

    const alertStyles = {
        fontFamily: "Lexend",
        fontWeight: 300,
    }

    //Captcha
    const [captchaVerified, setCaptchaVerified] = useState(false);
    const [token, setToken] = useState(null);
    const [done, setDone] = useState(false);
    const captchaRef = useRef(null);

    const onVerify = (token) => {
        setCaptchaVerified(true);
        setToken(token);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if (captchaVerified) {
            // console.log(token);
            setDone(true)
        } else {
            alertRef.current('Vui lng xc minh captcha trc khi gi! (Captcha c dng  gim vote o)');
        }
    };

    const alertRef = useRef(null);


    return (
        <div className="sendResult">
            <Category
                title="Xc nhn v Gi bnh chn"
                description="Di y l phn tm tt cc la chn m bn  chn trc . Vui lng kim tra k thng tin v xc nhn li trc khi gi bnh chn cui cng cho chng ti. Xin cm n!"
            />
            <div className="votedContent">
                <Card>
                    <div className="infoContent">
                        <Alert message={<span>Chng ti s dng thng tin v a ch IP  trnh spam v lc vote o! <strong>Thng tin ny s c trch xut t ng.</strong></span>} type="info" showIcon />
                        <div className="ipInfo">
                            <Card>
                                <Statistic
                                    title="a Ch IPv4"
                                    value={ipAddress}
                                    valueStyle={{
                                        color: '#3f8600',
                                    }}
                                    prefix={<GlobalOutlined />}
                                />
                            </Card>
                            <Card>
                                <Statistic
                                    title="a Ch IPv6"
                                    value={ip6Address}
                                    valueStyle={{
                                        color: '#3f8600',
                                    }}
                                    prefix={<GlobalOutlined />}
                                />
                            </Card>
                            <Card>
                                <Statistic
                                    title="Quc Gia"
                                    value={country}
                                    valueStyle={{
                                        color: '#cf1322',
                                    }}
                                    prefix={<EnvironmentOutlined />}
                                />
                            </Card>
                            <Card>
                                <Statistic
                                    title="Tinh/Thnh Ph"
                                    value={region}
                                    valueStyle={{
                                        color: '#cf1322',
                                    }}
                                    prefix={<HomeOutlined />}
                                />
                            </Card>
                        </div>
                    </div>
                </Card>

                {result.map((resultItem, index) => (
                    <Card key={index} title={<span style={cardTitleStyles}>{(index + 1) + ". " + resultItem.name}</span>}>
                        <div className="listItem">
                            {resultItem.chosenItems.map((ln, index) => (
                                <div key={index} className="rItem">
                                    <img alt="" src={`images/ln/${ln.cover}`} />
                                    <div className="info">
                                        <h4>{ln.name}</h4>
                                        <p>NXB: {ln.nxb}</p>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </Card>
                ))}

                <div className="captcha">
                    {!done && (
                        <form onSubmit={handleSubmit}>
                            <HCaptcha
                                sitekey="10000000-ffff-ffff-ffff-000000000001"
                                onVerify={onVerify}
                                ref={captchaRef}
                            />
                            <div className="btnContainer">
                                <button type="submit">
                                    Gi Bnh Chn
                                </button>
                            </div>
                        </form>
                    )}
                    {done && (
                        <div className="showSuccess">
                            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
                                <path strokeLinecap="round" strokeLinejoin="round" d="M9 12.75 11.25 15 15 9.75M21 12a9 9 0 1 1-18 0 9 9 0 0 1 18 0Z" />
                            </svg>
                            <h3>Bnh chn  c ghi nhn!</h3>
                            <p>Cm n bn  tham gia bnh chn cho bng xp hng ln ny. Chng ti s tng hp cc lt bnh chn v cng b kt qu ca bng xp hng trong thi gian sm nht!</p>
                        </div>
                    )}
                </div>

                <MessageHub>{(msg) => (alertRef.current = msg)}</MessageHub>

            </div>
        </div>
    );
}

export default SendResult;